import React, { Component } from 'react';
import axios from 'axios';

import Button from '../../components/Button';
import Loading from '../../components/Loading';

import './style.css';

class Product extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      id: props.match.params.id,
      data: {},
      loading: true,
    };
  }
  
  componentDidMount() {
    axios.get(`https://api.mercadolibre.com/items/${this.state.id}`)
      .then(({ data }) => {
        this.setState({ 
          data,
          loading: false,
        });
      });
  }

  renderContent() {
    const { data } = this.state;
    
    return (
      <div className="mdl-grid mdl-shadow--2dp">
        <div className="mdl-cell mdl-cell--6-col">
          <img alt={ data.title } src={ data.pictures[0].url } />
        </div>
        <div className="mdl-cell mdl-cell--6-col">
          <span className="item-id">#{ data.id }</span>
          <span className="sold-quantity">{ data.sold_quantity } vendidos</span>
          <h1 className="title">{ data.title }</h1>
          <span className="price">R$ { data.price }</span>

          <Button label="Comprar" />
        </div>
      </div>
    );
  }

  render() {
    const content = this.state.loading ?
      <Loading /> :
      this.renderContent();

    return (
      <section className="product container">
        <div className="mdl-grid">
          { content }
        </div>
      </section>
    )
  }
}
  
export default Product;